/* Шрифт */
@font-face {	
	font-family: "Gropled"; 
	src: url("./Gropled-Bold.otf") format("truetype"); 
	font-style: normal; 
	font-weight: normal; 
} 

/* Основные стили навигации */
.nav {
	padding: 20px 0;
	background-color: #000; /* Черный фон */
	border-bottom: 1px solid #333; /* Темно-серый бордер */
	color: #fff; /* Белый текст */
	letter-spacing: normal;
	position: fixed; /* Фиксируем навбар */
	top: 0;
	left: 0;
	width: 100%;
	z-index: 1000; /* Высокий z-index для навбара */
	transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out; /* Плавный переход для анимации */
}

/* Плавное скрытие и появление навбара */
.nav--visible {
	transform: translateY(0);
	opacity: 1;
}

.nav--hidden {
	transform: translateY(-100%);
	opacity: 0;
}

.nav-row {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	column-gap: 30px;
	row-gap: 20px;
	flex-wrap: nowrap;
	position: relative;
	z-index: 5; /* Уровень ниже, чем у навбара */
}

.btn-dark-mode {
	position: fixed; /* Фиксированное позиционирование */
	top: 10px;
	right: 10px;
	z-index: 20; /* Повышаем z-index для кнопки */
	cursor: pointer; /* Курсор указывает, что элемент кликабельный */
}

/* Логотип */
.logo {
    margin-right: auto;
    color: #fff; /* Белый цвет текста */
    font-size: 24px;
    font-family: 'Gropled', sans-serif; /* Подключение шрифта */
    position: relative;
    overflow: visible; /* Скрываем выходящий текст */
    display: inline-block;
    height: auto; /* Высота контейнера */
    line-height: normal; /* Высота строки */
    z-index: 1; /* Устанавливаем элемент на передний план */
    text-decoration: none; /* Убираем подчеркивание */
}

/* Анимация свечения */
@keyframes glow {
	0% {
		text-shadow: 0 0 0 rgba(255, 255, 255, 0); /* Без свечения */
	}
	100% {
		text-shadow: 0 0 10px rgba(255, 255, 255, 0.7); /* Свечение в конце */
	}
}

/* Анимация размытия и движения текста */
@keyframes slide-up-blur {
	0% {
		transform: translateY(0);
		opacity: 1;
		filter: blur(0px); /* Размытие в начале */
	}
	
	100% {
		transform: translateY(-100%);
		opacity: 0;
		filter: blur(3px); /* Убираем размытие в конце */
	}
}

@keyframes slide-down-blur {
	0% {
		transform: translateY(100%);
		opacity: 0;
		filter: blur(3px); /* Без размытия в начале */
	}
	100% {
		transform: translateY(0);
		opacity: 1;
		filter: blur(0px); /* Размытие в конце */
	}
}

/* Текст перед анимацией */
/* Псевдоэлементы наследуют размеры родителя */
.logo::before,
.logo::after {
    content: 'Донецкие аэрозоли'; /* Один и тот же текст */
    position: absolute;
    width: 100%;
    height: 100%; /* Растягиваем по высоте родительского элемента */
    top: 0;
    left: 0;
    display: block; /* Делаем блоком, чтобы учитывать размеры */
    pointer-events: none; /* Отключаем возможность взаимодействия */
}
/* Эффект при наведении */
.logo:hover::before,
.logo:hover::after {
    pointer-events: none; /* Отключаем события для псевдоэлементов */
}
/* Начальное состояние текста */
.logo::before {
	opacity: 1;
	transform: translateY(0);
	filter: blur(3px); /* Начальное размытие */
	text-shadow: 0 0 0 rgba(255, 255, 255, 0); /* Без свечения */
}

/* Текст, который поднимается */
.logo::after {
	transform: translateY(100%);
	opacity: 0;
	filter: blur(0); /* Без размытия в начале */
	text-shadow: 0 0 0 rgba(255, 255, 255, 0); /* Без свечения */
}

/* Эффект при наведении */
.logo:hover::before {
	animation: slide-up-blur 0.5s cubic-bezier(0.42, 0, 0.58, 1) forwards, glow 0.3s ease-out; /* Нелинейная анимация и свечение */
}

.logo:hover::after {
	animation: slide-down-blur 0.5s cubic-bezier(0.42, 0, 0.58, 1) forwards; /* Анимация при наведении */
}

/* Возврат к исходному состоянию при уходе мыши */
.logo:not(:hover)::before {
	animation: slide-down-blur 0.3s cubic-bezier(0.42, 0, 0.58, 1) forwards, glow 0.3s ease-out; /* Анимация при уходе */
}

.logo:not(:hover)::after {
	animation: slide-up-blur 0.3s cubic-bezier(0.42, 0, 0.58, 1) forwards; /* Анимация при уходе */
}

/* Стили для навигационных ссылок */
.nav-list {
	display: flex;
	flex-wrap: nowrap; /* Кнопки не будут переноситься */
	align-items: center;
	column-gap: 40px; /* Пространство между кнопками для ПК */
	font-size: 16px;
	font-weight: 500;
	font-family: 'Gropled';
	z-index: 5; /* Под навбаром, но выше фона */
}

/* Стили для навигационных ссылок */
.nav-list__link {
    color: #fff;
    transition: opacity 0.2s ease-in;
    text-align: center;
    position: relative;
    cursor: pointer;
    z-index: 10;
    pointer-events: all;
    display: inline-block; /* Убедитесь, что ссылка занимает необходимую область */
    padding: 0px; /* Увеличьте область клика */
}

.nav-list__link::before {
	content: '';
	position: absolute;
	left: 0;
	bottom: -3px;  /* Подчеркивание будет располагаться снизу */
	width: 0;  /* Изначально подчеркивание не видно */
	height: 2px;
	background-color: #fff; /* Белый цвет подчеркивания */
	transition: width 0.3s ease-in-out;  /* Анимация появления */
}

.nav-list__link:hover::before {
	width: 100%;  /* При наведении ширина подчеркивания увеличивается до 100% */
}

.nav-list__link--active::before {
	content: '';
	position: absolute;
	left: 0;
	bottom: -3px;
	width: 100%;
	height: 2px;
	background-color: #fff; /* Белый цвет подчеркивания */
}
/* Мобильная версия */
/* Отключаем анимации на мобильных устройствах */
@media (max-width: 1300px) {
    .logo::before,
    .logo::after {
        animation: none !important; /* Отключаем анимацию */
        transition: none !important; /* Отключаем переходы */
        transform: none !important; /* Убираем любые трансформации */
        filter: none !important; /* Убираем фильтры */
        opacity: 1 !important; /* Устанавливаем непрозрачность */
    }

    .logo:hover::before,
    .logo:hover::after {
        animation: none !important; /* Отключаем анимацию при наведении */
    }

    .logo:not(:hover)::before,
    .logo:not(:hover)::after {
        animation: none !important; /* Отключаем анимацию при уходе мыши */
    }

    .nav-list__link::before {
        transition: none !important; /* Отключаем переходы для подчеркивания */
    }
}
/* Мобильная версия */
@media (max-width: 768px) {
  .nav-row {
    flex-direction: column;
    align-items: center;
  }

  .logo {
    flex: 1 0 100%;
    text-align: center;
    margin-right: 0;
    margin-top: 25px;
    font-size: 30px;
	white-space: normal;
  }

  .btn-dark-mode {
    position: fixed; /* Изменение на фиксированное позиционирование */
    top: 10px;
    right: 10px;
    z-index: 20;
  }

  .nav-list {
    display: flex; /* Обязательно нужно, чтобы 'column-gap' работал */
  	justify-content: center; /* Центрируем кнопки */
  	column-gap: 50px; /* Увеличиваем расстояние между кнопками */
  	width: 100%;
  	padding: 8px 0;
  	z-index: 5; /* Устанавливаем z-index для мобильных версий */
  }

  .nav-list__link {
    width: auto;
    padding: 0px 0;
    font-size: 22px;
  }
}

@media (max-width: 500px) {
	.nav-list {
	  display: flex; /* Обязательно нужно, чтобы 'column-gap' работал */
		justify-content: center; /* Центрируем кнопки */
		column-gap: 30px; /* Увеличиваем расстояние между кнопками */
		width: 100%;
		padding: 18px 0;
		z-index: 5;
	}
}

@media (max-width: 400px) {
	.nav-list {
	  display: flex; /* Обязательно нужно, чтобы 'column-gap' работал */
		justify-content: center; /* Центрируем кнопки */
		column-gap: 20px; /* Увеличиваем расстояние между кнопками */
		width: 100%;
		padding: 8px 0;
		z-index: 5;
	}
}

@media (max-width: 360px) {
	.nav-list {
	  display: flex; /* Обязательно нужно, чтобы 'column-gap' работал */
		justify-content: center; /* Центрируем кнопки */
		column-gap: 10px; /* Увеличиваем расстояние между кнопками */
		width: 100%;
		padding: 8px 0;
		z-index: 5;
	}
}

/* Адаптивные стили для ультра узких экранов */
@media (max-width: 340px) {
	.nav-list {
		flex-direction: column; /* Кнопки располагаются друг под другом */
		column-gap: 0;
		row-gap: 10px;
		z-index: 5;
	}
}
