/* Header */
@font-face {	
	font-family: "Raleway"; 
	
	src: url("./Raleway-Bold.ttf") format("truetype"); 
	font-style: normal; 
	font-weight: normal; 
  } 

.header {
	padding: 40px 0;
	min-height: 695px;
	
	display: flex;
	justify-content: center;
	align-items: center;

	background-color: var(--header-bg);
	background-image: url('./img1.jpg');
	background-repeat: no-repeat;
	background-size: auto;
	background-position: center center;

	color: var(--header-text);
	text-align: center;
}

.header__wrapper {
	padding: 0 15px;
	max-width: 660px;
}

.header__title {
	margin-bottom: 20px;
	font-size: 40px;
	font-weight: 700;
	line-height: 1.4;
	font-family: 'Raleway';
}

.header__title strong {
	font-size: 60px;
	font-weight: 700;
}

.header__title em {
	font-style: normal;
	color:black;
}

.header__text {
	margin-bottom: 40px;
	font-size: 18px;
	line-height: 1.333;
	font-family: 'Raleway';
}

.header__text p + p {
	margin-top: 0.5em;
}